.menu {
  background-color: $menu-background-color;
  
  &__content {
    box-sizing: border-box;
    height: 100vh;
    padding: 29px 0;
    
    font-weight: 600;

    @include onTablet {
      padding: 34px 0;
    }

    @include onDeskTop {
      padding: 36px 0;
    }
  }

  &__top {
    @include top-actions;
    margin-bottom: 44px;
  }

  &__list {
    margin: 0 0 40px;
    padding: 0;
    list-style: none;
  }

  &__item {
    margin-bottom: 24px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__link {
    display: inline-block;
    padding-bottom: 8px;
    border-bottom: 1px solid transparent;
    
    font-size: 22px;
    line-height: 22px;
    color: $menu-text-color;
    text-decoration: none;
    letter-spacing: 2px;
    text-transform: uppercase;

    transition: border-color 0.3s;

    &:hover {
      border-color: $menu-text-color;
    }
  }

  &__call {
    display: block;
    width: max-content;
    font-weight: 600;
    text-transform: uppercase;
    color: $menu-text-color;
    text-decoration: none;

    &--phone {
      font-size: 16px;
      line-height: 27px;
      margin-bottom: 16px;
    }
    
    &--action {
      font-size: 12px;
      line-height: 16px;
      letter-spacing: 2px;
      padding-bottom: 8px;
      border-bottom: 1px solid $menu-text-color;
    }
  }
}
